@media screen  and (max-width: 540px) {
  .app-container {
    height: 200px;
  }
}

@media (min-width: 541px) and (max-width:768px) {
  .app-container {
    height: 250px;
  }
}

@media(min-width: 769px) and (max-width: 960px) {
  .app-container {
    height: 300px;
  }
}

@media(min-width: 961px) and (max-width: 1140px) {
  .app-container {
    height: 350px;
  }
}

@media(min-width: 1141px) {
  .app-container {
    height: 400px;
  }
}

